@font-face {
    font-family: 'Qager';
    src: local('Qager'), url(../../fonts/Qager.ttf) format('truetype');
    /* other formats include: 'woff2', 'truetype, 'opentype',
                              'embedded-opentype', and 'svg' */
  }

:root {
    --background-beige: rgba(228, 218, 196, 1);
    --black: rgba(0, 0, 0, 1);
    --green: rgba(66, 148, 80, 1);
    --iossafaribars-background-light: rgba(248, 248, 248, 0.88);
    --navy-accent: rgba(0, 0, 0, 1);
    --red-accent: rgba(148, 17, 32, 1);
    --teal-accent: rgba(62, 141, 144, 1);
    --white: rgba(255, 255, 255, 1);
  }

